@import url(https://fonts.googleapis.com/css?family=Source+Serif+Pro&display=swap);
@import url(https://fonts.googleapis.com/css?family=Raleway&display=swap);
.header {
  width: 100%;
  padding: 2rem 5rem;
  position: fixed;
  top: 0;
}

.dark .header {
  background-color: #202340;
}

.light .header {
  background-color: white;
}

.container {
  max-width: 50rem;
  padding: 5rem;
}

@media only screen and (max-width: 57rem) {
  .container {
    max-width: 50rem;
    padding: 5rem 2rem 5rem 2rem;
  }
}

@media only screen and (max-width: 34rem) {
  .container {
    max-width: 31rem;
    padding: 5rem 1.5rem 5rem 1.5rem;
  }
}

.index-title {
  text-decoration: none;
  font-size: 15pt;
  color: #202340;
}

.dark .index-title {
  color: #b1b1b3;
}

.index-content {
  padding-top: 0.75em;
  padding-bottom: 1.25em;
  color: #38383d;
}

.dark .index-content {
  color: #b1b1b3;
}

.content a {
  color: #38383d;
}

.dark .content a {
  color: #b1b1b3;
}

p, blockquote {
  padding-top: 1rem;
}

h2 {
  padding-top: 2.5rem;
}

.title,
.date,
.author {
  font-family: 'Raleway', sans-serif;
}

h1 {
  font-size: 32pt;
  text-decoration: none;
  color: #202340;
  margin: 0;
}

.dark h1 {
  color: #b1b1b3;
}

.dark .author, .dark .date {
  color: #b1b1b3;
}

.author, .date {
  font-size: 13pt;
  color: #38383d;
}

.content {
  padding-top: 1rem;
  padding-bottom: 3rem;
}

.content * {
  font-family: 'Source Serif Pro', serif;
  font-size: 14pt;
  line-height: 1.45em;
}

.content h2 {
  padding-left: 2%;
  border-bottom-style: solid;
  border-bottom-color: #b1b1b3;
  border-bottom-width: 1px;
  font-family: 'Raleway', sans-serif;
  color: #c44e27;
  font-size: 28pt;
  font-weight: 100;
  padding-bottom: 0;
}

.content h3 {
  font-family: 'Raleway', sans-serif;
  color: #c44e27;
  font-size: 20pt;
  margin-top: 0;
  margin-bottom: 0;
}

.content blockquote {
  font-size: 12.5pt;
  margin-right: 0;
}

h2.subhed {
  margin-bottom: 0;
}

h2.subhed + p {
  padding-top: 1rem;
  margin-top: 0;
}

h3.sub-subhed + p {
  padding-top: 1rem;
  margin-top: 0;
}

.content sub {
  font-size: 11pt;
}

* {
  font-family: monospace;
  font-size: 11pt;
}

.light {
  color: #202340;
}

.dark {
  background-color: #202340;
  color: #b1b1b3;
}

.header-span {
  padding-right: 5rem;
}

.header-link {
  text-decoration: none;
  color: #202340;
}

.dark .header-link {
  text-decoration: none;
  color: #b1b1b3;
}

@media only screen and (max-width: 57rem) {
  .header {
    width: 100%;
    padding: 2rem;
  }

  .header-span {
    padding-right: 3rem;
  }
}

@media only screen and (max-width: 34rem) {
  .header {
    width: 100%;
    padding: 1.5rem;
  }

  .header-span {
    padding-right: 1.2rem;
  }
}

